// Variables
//
// Variables should follow the `$component-state-property-size` formula for
// consistent naming. Ex: $nav-link-disabled-color and $modal-content-box-shadow-xs.


// Forms

$tbmd-input-transition:             border-color 180ms cubic-bezier(0.4, 0, 0.2, 1) !default;
$tbmd-input-placeholder-transition: color 180ms cubic-bezier(0.4, 0, 0.2, 1);
$tbmd-input-bottom-line-transition: background 0s ease-out;

$tbmd-control-indicator-size:          1.25rem !default;
$tbmd-control-indicator-border-color:  rgba(0, 0, 0, 0.54) !default; // $gray-600
$tbmd-control-indicator-border-radius: $border-radius !default;


$tbmd-checkbox-indicator-icon-checked: str-replace(url("data:image/svg+xml;charset=utf8,%3csvg xmlns='http://www.w3.org/2000/svg' fill='#{$custom-control-indicator-checked-color}' height='20' width='20' viewBox='0 0 24 24'%3e%3cpath d='M0 0h24v24H0z' fill='none'/%3e%3cpath d='M9 16.17L4.83 12l-1.42 1.41L9 19 21 7l-1.41-1.41z'/%3e%3c/svg%3e"), "#", "%23") !default;

